import { LoadingService } from './../../services/loading.service';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { SchoolService } from '../../services/school.service';
import { ParseService } from '../../services/parse.service';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs/Rx';

@Component({
  selector: 'sp-main-config',
  templateUrl: './main-config.component.html',
  styleUrls: ['./main-config.component.css']
})
export class MainConfigComponent implements OnInit, OnDestroy {

  school = this.ps.currentUser.get("school");

  commonName: FormControl = new FormControl(this.school.get("commonName") ? this.school.get("commonName") : '', Validators.required);

  isLoading = false;

  editForm: FormGroup;

  sub: Subscription;

  constructor(
    private ls: LoadingService,
    private ss: SchoolService,
    private ps: ParseService,
    private fb: FormBuilder,
    private router: Router,
    private route: ActivatedRoute) {
  }

  ngOnInit() {
    this.ls.setLoading(true);
    this.isLoading = true;
    this.editForm = this.fb.group({
      commonName: this.commonName,
      motto: [this.school.get('motto') ? this.school.get('motto') : ''],
      address: [this.school.get('address') ? this.school.get('address') : '', Validators.required]
    });
    this.sub = this.ss.getSchool().subscribe(
      s => {
        this.ls.setLoading(false);
        this.isLoading = false;
        this.school = s;
        this.editForm.setValue({
          commonName: this.school.get('commonName'),
          motto: this.school.get('motto'),
          address: this.school.get('address')
        });
      },
      err  => {
        console.log(err);
        this.isLoading = false;
        this.ls.setLoading(false);
        if (err.code === 100) {
          // this.router.navigate(['50x']);
        }
      }
    );
  }

  ngOnDestroy(): void {
    this.ls.setLoading(false);
    this.sub.unsubscribe();
  }

  updateSchool() {
    this.ls.setLoading(true);
    this.isLoading = true;
    this.ss.updateSchool(this.editForm.value).subscribe(
      s => {
        this.ls.setLoading(false);
        this.isLoading = false;
      }, e => {
        this.ls.setLoading(false);
        this.isLoading = false;
        console.log(e);
      }
    );
  }

}



