import { LayoutModule } from './../commons/layout/layout.module';
import { BasicComponent } from '../commons/layout/basic/basic.component';
import { SchoolResolver } from './../resolvers/school.resolver';
import { ReactiveFormsModule } from '@angular/forms';
import { UserService } from './../services/user.service';
import { LoadingService } from './../services/loading.service';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SchoolComponent } from './school.component';
import { RouterModule, Routes } from '@angular/router';
import {
  MatTabsModule,
  MatCardModule,
  MatInputModule,
  MatButtonModule,
  MatPaginatorModule,
  MatAutocompleteModule,
  MatDatepickerModule,
  MatNativeDateModule} from '@angular/material';
import { ParseService } from '../services/parse.service';
import { RouteService } from '../services/route.service';
import { AddAdminComponent } from './add-admin/add-admin.component';
import { MainConfigComponent } from './main-config/main-config.component';
import { WidgetModule } from '../commons/widget/widget.module';
import { SchoolService } from '../services/school.service';
import { AddTeacherComponent } from './add-teacher/add-teacher.component';
import { TermComponent } from './term/term.component';
import { NotTeacherOrAdminGuard } from '../guards/not-teacher-or-admin.guard';

export const ROUTES: Routes = [
  {
    path: '',
    component: BasicComponent,
    canActivate: [
      NotTeacherOrAdminGuard
    ],
    children: [
      {
        path: '',
        component: SchoolComponent,
        children: [
          {
            path: 'admin',
            component: AddAdminComponent
          },
          {
            path: 'main',
            component: MainConfigComponent
          },
          {
            path: 'teacher',
            component: AddTeacherComponent
          },
          {
            path: 'term',
            component: TermComponent
          }
        ]
      }
    ]
  }
]

@NgModule({
  imports: [
    CommonModule,
    LayoutModule,
    MatAutocompleteModule,
    MatButtonModule,
    MatCardModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatInputModule,
    MatPaginatorModule,
    MatTabsModule,
    ReactiveFormsModule,
    RouterModule.forChild(ROUTES),
    WidgetModule
  ],
  declarations: [SchoolComponent, AddAdminComponent, MainConfigComponent, AddTeacherComponent, TermComponent],
  providers: [
    NotTeacherOrAdminGuard,
    SchoolResolver,
    SchoolService
  ]
})
export class SchoolModule { }



