import { ITerm } from './../../model/ITerm';
import { SchoolService } from './../../services/school.service';
import { Component, OnInit } from '@angular/core';
import { ParseService } from '../../services/parse.service';
import * as moment from 'moment';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'sp-term',
  templateUrl: './term.component.html',
  styleUrls: ['./term.component.css']
})
export class TermComponent implements OnInit {

  user: any;
  school: any;
  term: any;

  termForm: FormGroup;

  isNewTermEdit = false;

  constructor(private ps: ParseService, private ss: SchoolService, fb: FormBuilder) { 
    this.termForm = fb.group({
      editTitle: ['', Validators.required],
      startDate: ['', Validators.required],
      endDate: ['', Validators.required]
    });
    this.termForm.controls.endDate.valueChanges.subscribe(
      date => {
        const d = moment(date, 'MM/DD/YYYY').toDate();
        if (this.beforeOrSame(d)) {
          this.termForm.controls.endDate.setErrors({hasVacated: true});
        }
      }
    );
  }

  ngOnInit() {
    this.ss.getTerm().subscribe(
      (s: any) => {
        this.term = s;
        this.termForm.patchValue({
          editTitle: s.get('title'),
          startDate: moment(s.get('startDate')).format('MM/DD/YYYY'),
          endDate: moment(s.get('endDate')).format('MM/DD/YYYY')
        });
        if (this.after(s.get('startDate'))) {
          this.termForm.controls.startDate.disable();
        }

        if (this.beforeOrSame(s.get('endDate'))) {
          this.termForm.controls.endDate.disable();
        } else {
          this.isNewTermEdit = true;
        }
      }
    );
  }

  updateTerm() {
    console.log(this.termForm.value);
    if (!this.isNewTermEdit) {
      this.termForm.controls.startDate.enable();
      this.termForm.controls.endDate.enable();
      this.isNewTermEdit = true;
    } else {
      const term: ITerm = {
        startDate: moment(this.termForm.controls.startDate.value).add(8, 'hours').toDate(),
        endDate: moment(this.termForm.controls.endDate.value).add(8, 'hours').toDate(),
        title: this.termForm.controls.editTitle.value
      };

      this.ss.updateTerm(this.term, true, term).subscribe(
        t => {
          this.isNewTermEdit = false;
        },
        err => {
          console.log(err);
        }
      );
    }
  }

  newTerm() {
    if (!this.isNewTermEdit) {
      this.termForm.patchValue({
        startDate: moment().format('MM/DD/YYYY'),
        endDate: moment().add(3, 'months').format('MM/DD/YYYY')
      });
      this.termForm.controls.startDate.enable();
      this.termForm.controls.endDate.enable();
      this.isNewTermEdit = true;
    } else {
      const term: ITerm = {
        startDate: moment(this.termForm.controls.startDate.value).toDate(),
        endDate: moment(this.termForm.controls.endDate.value).toDate(),
        title: this.termForm.controls.editTitle.value
      };

      this.ss.createTerm(this.term, term).subscribe(
        t => {
          this.term = t;
          this.isNewTermEdit = false;
        },
        err => {
          console.log(err);
        }
      );
    }
  }

  beforeOrSame(endDate: Date): boolean {
    return moment(endDate).isSameOrBefore(moment());
  }

  after(startDate: Date): boolean {
    return moment(startDate).isBefore(moment());
  }

}



