import { ITerm } from './../model/ITerm';
import { UserService } from './user.service';
import { ISchool } from './../model/ISchool';
import { Observable, Subject } from 'rxjs/Rx';
import { Injectable } from '@angular/core';
import { ParseService } from './parse.service';

const Parse = require('parse');

@Injectable()
export class SchoolService {

  admins: any = {};

  adminSubject: Subject<any> = new Subject<any>();

  school = this.ps.currentUser.get('school');

  constructor(private ps: ParseService, private us: UserService) {
    this.getAdmin().subscribe(
      a => {
        this.admins = a;
        this.adminSubject.next(this.admins);
      }
    );
  }

  getSchool() {
    return new Observable(observer => {
      this.school.fetch().then((s) => {
        observer.next(s);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  getTerm() {
    return this.getSchool().flatMap((s: any) => {
      return new Observable(observer => {
        s.get('term').fetch().then((t) => {
          observer.next(t);
          observer.complete();
        }, err => {
          observer.error(err);
        });
      });
    });
  }

  updateSchool(values) {
    return new Observable(observer => {
      this.school.set('commonName', values.commonName);
      this.school.set('motto', values.motto);
      this.school.set('address', values.address);

      this.school.save().then((s) => {
        observer.next(s);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  createTerm(currentTerm: any, newTerm: ITerm) {
    const newT = this.newTerm(newTerm);
    const oldT = this.updateTerm(currentTerm, false);

    let term: any;

    return oldT.switchMap(() => newT)
      .flatMap((t: any) => {
        term = t;
        const school = this.ps.currentUser.get('school');
        school.set('term', t);
        return this.ps.save(school);
      }).map(() => term);
  }

  updateTerm(currentTerm: any, isNewTerm: boolean, newTerm?: ITerm) {
    currentTerm.set('isCurrentTerm', isNewTerm);
    if (newTerm) {
      currentTerm.set('startDate', newTerm.startDate);
      currentTerm.set('endDate', newTerm.endDate);
      currentTerm.set('title', newTerm.title);
    }
    return this.ps.save(currentTerm);
  }

  newTerm(term: ITerm) {
    const t = new Parse.Object('Term');
    t.set('startDate', term.startDate);
    t.set('endDate', term.endDate);
    t.set('title', term.title);
    t.set('isCurrentTerm', true);
    return this.ps.save(t);
  }


  getAdmin(limit = 10, skip = 0) {
    return this.us.getUsersByRole(this.school, 'admin', limit, skip);
  }

  getTeachers(limit = 10, skip = 0) {
    return this.us.getUsersByRole(this.school, 'teacher', limit, skip);
  }

  removeAdmin(id) {
    const user = this.us.getUserPointer(id);
    return new Observable(observer => {
      Parse.Cloud.run('removeAdmin', {user: user}).then((res) => {
        observer.next(res);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  addAdmin(id) {
    const user = this.us.getUserPointer(id);
    return new Observable(observer => {
      Parse.Cloud.run('addAdmin', {user: user}).then((res) => {
        observer.next(res);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }
  addTeacher(id) {
    const user = this.us.getUserPointer(id);
    return new Observable(observer => {
      Parse.Cloud.run('addTeacher', {user: user}).then((res) => {
        observer.next(res);
        observer.complete();
      }, err => {
        observer.error(err);
      });
    });
  }

  search(term) {
    return this.us.searchUserByTerm(term, 'none')
      .flatMap((u: any[]) => Observable.from(u))
      .map(user => {
        const u = {
          id: user.id,
          firstName: user.get('firstName'),
          lastName: user.get('lastName'),
          email: user.get('email')
        };
        return u;
      }).toArray();
  }

}



