import { Subscription } from 'rxjs/Rx';
import { SchoolService } from '../../services/school.service';
import { ChangeDetectionStrategy, Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { LoadingService } from '../../services/loading.service';
import { IEvent, EventType } from '../../model/IEvent';
import { Router } from '@angular/router';
import { FormControl } from '@angular/forms';
import { getUserItem } from '../school-util';

@Component({
  selector: 'sp-add-admin',
  templateUrl: './add-admin.component.html',
  styleUrls: ['./add-admin.component.css']
})
export class AddAdminComponent implements OnInit {

  userCtrl: FormControl;
  filteredUsers: any;

  admins: any = {};
  sub: Subscription;
  dataset: any[] = [];

  isSearching = false;
  shoudSearch = true;

  constructor(private ls: LoadingService, private ss: SchoolService, private router: Router, private cd: ChangeDetectorRef) {

  }

  ngOnInit() {
    this.ls.setLoading(true);
    this.userCtrl = new FormControl();
    this.sub = this.ss.getAdmin().subscribe(
      u => {
        this.ls.setLoading(false);
        this.admins = u;
        const ds: any[] = [];
        for (let i = 0; i < this.admins.users.length; i++) {
          const item: any = getUserItem(this.admins.users[i], i);
          ds.push(item);
        }

        this.dataset = ds;
        // this.cd.markForCheck();
      },
      err => {
        this.ls.setLoading(false);
        console.log(err);
      }
    );

    this.filteredUsers = this.userCtrl.valueChanges
      .debounceTime(500)
      .filter(t => this.shoudSearch)
      .switchMap(t => this.ss.search(t));
  }

  doEvent(e: IEvent) {
    // console.log(e);
    if (e.event === EventType.view) {
      this.router.navigate(['/profile', e.objectId]);
    } else {
      this.ls.setLoading(true);
      this.ss.removeAdmin(e.objectId).subscribe(
        s => {
          this.ls.setLoading(false);
          this.dataset = this.dataset.filter(i => i.objectId != e.objectId);
          this.dataset.forEach(i => {
            if (i.id > 1) {
              i.id--;
            }
          });
          this.admins.count--;
        }, err => {
          this.ls.setLoading(false);
        }
      );
    }
  }

  toggleSearch() {
    this.isSearching = !this.isSearching;
  }

  assignAdmin(user) {
    console.log(user);
    this.shoudSearch = false;
    this.userCtrl.disable();
    this.ls.setLoading(true);
    this.ss.addAdmin(user.id).subscribe(
      a => {
        console.log(a);
        this.dataset.forEach(i => {
          i.id++;
        });
        const item: any = getUserItem(a, 0);
        this.dataset.unshift(item);
        this.ls.setLoading(false);
        this.isSearching = false;
        this.userCtrl.enable();
        this.userCtrl.reset();
        this.admins.count++;
        this.shoudSearch = true;
      },
      err => {
        this.ls.setLoading(false);
        this.userCtrl.enable();
        this.shoudSearch = true;
        console.log(err);
      }
    );
  }

}



